import callToActionStyles from './CallToAction.module.css';
import appStyles from './App.module.css';
import { SearchCriteria } from './types';
import { useHistory } from 'react-router-dom';
import { BannerButton } from './Buttons';
import banner from './images/hero-banner.jpg';

type CallToActionProps = {
  setSearchCriteria: React.Dispatch<React.SetStateAction<SearchCriteria>>;
};

export function CallToAction({ setSearchCriteria }: CallToActionProps) {
  const history = useHistory();

  function browseAllBooks() {
    // TODO: Use Book context function clearSearch()
    setSearchCriteria({
      genres: [],
      media: [],
      searchText: '',
      tags: [],
    });

    history.push('/search');
  }

  return (
    <div className={callToActionStyles['banner-container']}>
      <div className={`${appStyles.container} ${callToActionStyles.banner}`}>
        <div>
          <h1 className={callToActionStyles['header-text']}>Buy Books Directly from the Author!</h1>
          <hr className={callToActionStyles['beautiful-rule']} />
          <p>Put 90%+ straight into your favorite author's pocket.</p>
          <p>
            <BannerButton onClick={browseAllBooks}>
              <i className="fas fa-book"></i> See What's New
            </BannerButton>
          </p>
        </div>
        <img
          className={callToActionStyles['banner-image']}
          src={banner}
          alt="A formidable multi-story library"
        />
      </div>
    </div>
  );
}
